import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-roleshome',
  templateUrl: './roleshome.component.html',
  styleUrls: ['./roleshome.component.css']
})
export class RoleshomeComponent implements OnInit {

  addRoleButtonStatus: boolean;

  constructor(private router: Router, private route: ActivatedRoute) {
  }

  ngOnInit() {
    this.addRoleButtonStatus = this.router.url === '/roles';
  }

  onNewRole() {
    this.router.navigate(['addRole'], {relativeTo: this.route});
  }

}
