import {Component, OnInit} from '@angular/core';
import {FormControl, FormGroup} from '@angular/forms';
import {FieldsModel} from '../models/fields.model';
import {SubmitOptionModel} from '../models/SubmitOption.model';
import {ActivatedRoute, Router} from '@angular/router';
import {HttpService} from '../../shared/http.service';

@Component({
  selector: 'app-addroles',
  templateUrl: './addroles.component.html',
  styleUrls: ['./addroles.component.css']
})
export class AddrolesComponent implements OnInit {
  selectOptions: SubmitOptionModel[] = [];
  dynamicForm: FormGroup;
  fields: FieldsModel[] = [];
  formValue: SubmitOptionModel[] = [];

  constructor(private route: Router, private httpService: HttpService) {
  }

  ngOnInit() {
    this.formInit();
  }

  private formInit() {
    this.dynamicForm = new FormGroup({
      name: new FormControl(''),
      label: new FormControl(''),
      type: new FormControl(''),
      value: new FormControl(''),
      placeholder: new FormControl(''),
      optionValue: new FormControl(''),
      optionLabel: new FormControl(''),
      oldOptionLabel: new FormControl(''),
      oldOptionValue: new FormControl('')
    });
  }

  onSubmit() {
    console.log(this.dynamicForm.value);
    console.log(this.selectOptions);
  }

  onAddSelectOption() {
    const obj = new SubmitOptionModel(this.dynamicForm.get('optionLabel').value, this.dynamicForm.get('optionValue').value);
    this.selectOptions.push(obj);

    this.dynamicForm.get('optionLabel').patchValue('');
    this.dynamicForm.get('optionValue').patchValue('');
  }

  onChangeSelectOption(i: number) {
    const obj = new SubmitOptionModel(this.dynamicForm.get('oldOptionLabel').value, this.dynamicForm.get('oldOptionValue').value);
    this.selectOptions[i] = obj;
    console.log(this.selectOptions);
  }

  onDeleteSelectOption(i: number) {
    this.selectOptions.splice(i, 1);
  }

  onDeleteField(i: number) {
    this.fields.splice(i, 1);
  }


  onAddNewFormOption() {
    if (this.dynamicForm.get('type').value === 'select') {
      this.formValue = this.selectOptions;
    } else {
      this.formValue.push(new SubmitOptionModel('', this.dynamicForm.get('value').value));
    }
    const obj = new FieldsModel(
      this.dynamicForm.get('name').value,
      this.dynamicForm.get('type').value,
      this.dynamicForm.get('placeholder').value,
      this.dynamicForm.get('label').value,
      this.dynamicForm.get('value').value
    );
    this.fields.push(obj);
    this.dynamicForm.get('type').patchValue('');
    this.dynamicForm.get('placeholder').patchValue('');
    this.dynamicForm.get('name').patchValue('');
    this.dynamicForm.get('label').patchValue('');
    this.dynamicForm.get('value').patchValue('');
  }

}
