import {RouterModule, Routes} from '@angular/router';
import {NgModule} from '@angular/core';
import {BusinessComponent} from './business.component';
import {BusinesslistComponent} from './businesslist/businesslist.component';

const businessRoutes: Routes = [
  {
    path: '', component: BusinessComponent, children: [
      {path: 'list', component: BusinesslistComponent},
      {
        path: 'transportationBusiness',
        loadChildren: () => import('./transportation/transportation.module').then(module => module.TransportationModule)
      },
      {
        path: 'propertiesBusiness',
        loadChildren: () => import('./properties/properties.module').then(module => module.PropertiesModule)
      },
      {
        path: 'serviceSupplierBusiness',
        loadChildren: () => import('./servicesupplier/servicesupplier.module').then(module => module.ServicesupplierModule)
      },
      {
        path: 'realEstateBusiness',
        loadChildren: () => import('./realestate/realestate.module').then(module => module.RealestateModule)
      },
      {
        path: 'financeBusiness',
        loadChildren: () => import('./finance/finance.module').then(module => module.FinanceModule)
      },
      {
        path: 'travelAgentBusiness',
        loadChildren: () => import('./travelagent/travelagent.module').then(module => module.TravelagentModule)
      },
      {
        path: 'travelCuratorBusiness',
        loadChildren: () => import('./travelcurator/travelcurator.module').then(module => module.TravelcuratorModule)
      },
      {
        path: 'jobsBusiness',
        loadChildren: () => import('./jobs/jobs.module').then(module => module.JobsModule)
      },
      {
        path: 'treksBusiness',
        loadChildren: () => import('./treks/treks.module').then(module => module.TreksBusinessModule)
      },
      {
        path: 'eventsBusiness',
        loadChildren: () => import('./events/events.module').then(module => module.EventsModule)
      },
      {
        path: 'roadTripBusiness',
        loadChildren: () => import('./roadtrip/roadtrip.module').then(module => module.RoadtripModule)
      },
      {
        path: 'myProfile',
        loadChildren: () => import('./myprofile/myprofile.module').then(module => module.MyprofileModule)
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(businessRoutes)],
  exports: [RouterModule]
})

export class BusinessRoutingModule {

}
