import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {RoleslistComponent} from './roleslist/roleslist.component';
import {BusinessComponent} from './business/business.component';

const roleRoutes: Routes = [
  {
    path: '', component: RoleslistComponent, children: [
      // roles for business
      {
        path: 'b', loadChildren: () => import('./business/business.module').then(module => module.BusinessModule)
      },
    ]
  },

];

@NgModule({
  imports: [RouterModule.forChild(roleRoutes)],
  exports: [RouterModule]
})

export class RolesRoutingModule {

}
