import {Component, OnInit} from '@angular/core';
import {Store} from '@ngrx/store';
import {ActivatedRoute, Router} from '@angular/router';
import {HttpService} from '../../../shared/http.service';
import {ToastService} from '../../../shared/toast/toast.service';
import {SidebarService} from './sidebar.service';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {
  roles = [];
  name = '';
  profilePic = '../../../../assets/icons/demo.jpg';

  constructor(public sidebarService: SidebarService, private store: Store<any>, private route: ActivatedRoute, private http: HttpService, private router: Router, private toastService: ToastService) {
  }

  ngOnInit() {
    this.store.select('authState').subscribe(
      (value) => {
        this.roles = [];
        this.name = value.profile.name;
        if (value.profile.profileImage) {
          this.profilePic = value.profile.profileImage;
        }
        for (const x in value.profile.roles.business) {
          if (value.profile.roles.business[x]) {
            console.log(x);
            this.roles.push(x);
          }
        }
      }
    );

    if (this.roles.length === 0) {
      this.toastService.showDefault('You can also Create your Business Profiles in Edit Profile Section');
    }
  }

}
