import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class SidebarService {

  visible = true;

  constructor() {
    this.visible = true;
  }

  hide() {
    // alert('hide');
    this.visible = false;
  }

  show() {
    // alert('show');
    this.visible = true;
  }

}
